* {
  margin: 0;
  user-select: none;
}
.wrapper {
  margin-bottom: 20px;
}
.wrpaperH2 {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 30px;
}
.h2 {
  font-weight: normal;
  font-size: 30px;
  text-transform: uppercase;
  color: #222222;
  line-height: 1;
  font-family: "Caviar Dreams";
}
.h3 {
  color: #222222;
  font-size: 22px;
  font-weight: normal;
  text-align: center;
  font-family: "Caviar Dreams";
  text-transform: uppercase;
  line-height: 1;
}
.span {
  color: rgba(105, 201, 202, .4);
  font-size: 48px;
  display: inline-block;
  transform: translate(35px, 0);
  font-family: "Open Sans";
  font-weight: bold;
}
@media(min-width: 1200px) {
  .wrapper {
    margin-bottom: 2vh;
  }
  .wrpaperH2 {
    height: 100px;
    background: no-repeat url('../../../img/elements/buttonsGroup.png');
    background-position: 40% 50%;
    margin-bottom: 7vh;
    margin-top: 5vh;
  }
  .h2 {
    line-height: 100px;
    font-size: 60px;
  }
  .h3 {
    text-align: left;
    font-size: 48px;
    margin-left: 100px;
  }
  .span {
    font-size: 100px;
    transform: translate(80px, 0);
  }
}
