.section {
  height: 690px;
  clip-path: polygon(0% 0%, 0% 95%, 100% 100%, 100% 0%);
  padding-top: 20px;
  background: no-repeat url('../../img/background/secondScreenSomething.png');
  background-position: 100% 2%;
  background-size: 10%;
  overflow: hidden;
}

@media(min-width: 1200px) {
  .section {
    height: 100vh;
    padding-top: 50px;
    background: none;
    clip-path: polygon(0% 0%, 0% 90%, 100% 100%, 100% 0%);
    position: relative;
  }
  .img {
    display: block;
    position: absolute;
    height: auto;
    width: 45vw;
    left: 60%;
    bottom: 5%;
  }
  .navigation {
    position: absolute;
    left: 60px;
    top: 30vh;
  }
}
