.wrapper {
  width: 100%;
}
.text {
  font-size: 14px;
  font-weight: lighter;
  margin-bottom: 20px;
  padding: 0 7vw;
}
.img {
  display: block;
  margin: 0 auto;
  height: 220px;
  width: auto;
  margin-bottom: 30px;
}
.buttons {
  padding: 0 7vw;
}

@media(min-width: 1200px) {
  .wrapper {
    width: 35vw;
    padding: 0;
    margin-left: 218px;
  }
  .text {
    padding: 0;
  }
  .list {
    list-style: none;
    display: flex;
    justify-content: space-between;
    margin-bottom: 7vh;
    padding: 0;
  }
  .listItemLink {
    color: #575757;
    text-decoration: none;
    transition: all .4s ease;
    display: inline-block;
  }
  .listItemLink i {
    font-size: 40px;
  }
  .listItemLink:hover {
    color: #69c9ca;
    text-decoration: none;
  }
}
